const uni = require('uni').default;









































































































































import { mapState, mapGetters, mapMutations } from 'vuex'
import fotermenu from '../../components/menu.vue'	
import txtmessage from '../../components/message.vue'

export default {
	data(){
		return {
			isdown: true,
			iszan: false,
			mes: {
				
			}
		}
	},
	computed: {
		...mapState([
			'HTTPS_URL'
		])
	},
	components: {
		'fotermenu': fotermenu,
		'txtmessage': txtmessage
	},
	mounted(){
		
	},
	onLoad(query){
		this.bindMP(query.id)
	},
	methods:{
		bindMP(id){
			console.log('ID', id)
			let _this = this
			uni.request({
				url: `${this.HTTPS_URL}/businessCard/get?cardId=5ba39565b8a903b80c48ed46`,
				method: 'GET',
				success:function(res){
					console.log(res.data.data)
					if(res.data.status === 1){
						_this.mes = res.data.data.cardInfo
						console.log('this is message:', this.mes)
					}
				}
			})
		},
		// 展开
		down(){
			this.isdown = !this.isdown
		},
		// 点赞 
		zan(){
			this.iszan = !this.iszan
		},
		// 拨打电话 
		call(nub){
			uni.makePhoneCall({
				phoneNumber: nub
			})
		},
		// 复制
		copy(nub){
			uni.setClipboardData({
				data: nub,
				success:function(){
					uni.showToast({
						title: '复制成功'
					})
				}
			})
		},
		addphone(){
			uni.addPhoneContact({
				photoFilePath: '../../static/images/touxiang.jpg',
				nickName: '姓名',
				lastName: '吕',
				middleName: '希强',
				firstName: '希强',
				remark: '备注',
				mobilePhoneNumber: '17600038869',
				weChatNumber: '17600038869',
				addressCountry: '中国',
				addressCity: '北京',
				addressState: '北京',
				addressStreet: '朝阳区朝阳路',
				organization: '公司',
				title: '前端',
				hostNumber: '17600038869',
				email: '18664698@qq.com',
				success: (res)=>{
					uni.showToast({
						title: '保存成功',
						icon: 'success',
					})
				}
			})
		}
	},
	
	
	onShareAppMessage: function (res) {
		if (res.from === 'button') {
		  // 来自页面内转发按钮
		  console.log(res.target)
		}
		return {
		  title: '自定义转发标题',
		  path: '/page/user?id=123',
		  imageUrl: '../../static/images/touxiang.jpg'
		}
	}
}



