const uni = require('uni').default;




























export default {
	data(){
		return {
			mesage: {
				title: '消息内容',
				mesg: '您有一条消息体'
			},
			animation: {}
		}
	},
	methods: {
		getPage(){
			let pages = getCurrentPages()
			let nowpage = pages[pages.length - 1]
			let route = nowpage.route
			let txt = 'pages/page/'
			let routename = route.substring(txt.length, route.length)
			return routename
		},
		// 动画 显示
		show(){
			let ani =  wx.createAnimation({
			  duration: 500,
			  timingFunction: 'ease',
			})
			this.ani = ani
			ani.opacity(1).translateY(0).step()
			this.animation = ani.export()
			
			setTimeout(function(){
				this.hide()
			}.bind(this), 4000)
		},
		// 动画 隐藏
		hide(){
			let ani =  wx.createAnimation({
			duration: 500,
			timingFunction: 'ease',
			})
			this.ani = ani
			ani.opacity(0).translateY('-200px').step()
			this.animation = ani.export()
		}
	},
	onLoad:function(){
		this.pagename = this.getPage()
	},
	mounted(event){
		this.show()
	}
}



